import React from 'react';
import Table from '../../Table/Table';
import { formatDate } from '../../../utils';

function TransactionsTable({ transactions, onRowClick, selectedRow }) {
  const columns = [
    { title: '', format: date => formatDate(date) },
    { title: '' },
    { title: '', styles: { style: { maxWidth: '245px' }, condition: () => true } },
    { title: ' ' },
    { title: '', styles: { 
      style: { color: '#8AB65A', opacity: 0.4 },
      style: { color: '#8AB65A' }, condition: value => value === '' 
    } 
    }
    
  ];

  return (
    <Table 
      columns={ columns } 
      data={ transactions } 
      onRowClick={ onRowClick }
      selectedRow={ selectedRow }
      clickableCondition={ item => item.status === '' }
    />
  );
}

export default TransactionsTable;