import React, {useRef} from 'react';
import Table from '../../../Table/Table';
import { formatDate } from '../../../../utils';
import SentScanModal from "../../../modals/SentScan/SentScanModal";
import RoleBasedRender from '../../../RoleBasedRender/RoleBasedRender';

function DocumentsActsTable({ documents, onRowClick, onDelete }) {
  const childRef = useRef();
  const columns = [
    { title: '', format: date => formatDate(date) },
    { title: '' },
    { title: '' },
    { title: '', styles: { style: { color: '#8AB65A' }, condition: value => value === ''}}
  ];

  const columns2 = [
    { title: '', format: date => formatDate(date) },
    { title: '' },
    { title: '', styles: { style: { color: '#8AB65A' }, condition: value => value === ''}}
  ];

  const actions = [
  <RoleBasedRender requiredRoles={ [''] } >
    <button className={ 'secondary-button hidden' } onClick={() => childRef.current.showModal()}>   </button>
  </RoleBasedRender>
  ];

  return (
    <div>
      <RoleBasedRender requiredRoles={ [''] } >
        <Table 
          columns={ columns } 
          data={ documents } 
          onDelete= { onDelete }
          actions={ actions } 
          onRowClick={ onRowClick }
        />
      </RoleBasedRender>
      <RoleBasedRender requiredRoles={ [''] } >
        <Table 
          columns={ columns2 } 
          data={ documents } 
          onDelete= { onDelete }
          actions={ actions } 
          onRowClick={ onRowClick }
        />
      </RoleBasedRender>
      <SentScanModal ref={childRef}/>
    </div>
  );
}

export default DocumentsActsTable;