import React from 'react';
import Table from '../../../Table/Table';

function StatisticObjectTable({ objects, onRowClick, onDelete }) {
  const columns = [
    { title: '' },
    { title: '' },
    { title: '' },
    { title: '' }
  ];

  const actions = [
    <button className={ 'secondary-button hidden' } onClick={ onDelete }>  </button>
  ];

  return (
    <Table 
      columns={ columns } 
      data={ objects } 
      onDelete= { onDelete }
      actions={ actions } 
      onRowClick={ onRowClick }
    />
  );
}

export default StatisticObjectTable;