import React from 'react';
import Table from '../../Table/Table';
import './TableFix.scss';
import { formatDate } from '../../../utils';
import RoleBasedRender from '../../RoleBasedRender/RoleBasedRender';

function ReadyTable({ objects, onRowClick, onDelete }) {
  const columns = [
    { title: '', accessor: value => value.object },
    { title: '', 
      subColumns: [
        { title:'',  accessor: value => value.status, format: value => value ? value : '', styles: { style: { color: '#8AB65A' }, condition: value => value === '' } },
        { title:' ', accessor: value => value.consumption, format: value => value ? value : '', styles: { style: { color: 'rgba(0, 0, 0, 0.4)' }, condition: value => value === null } }, 
      ],
    }
  ];

  const columns2 = [
    { title: '', format: date => formatDate(date) },
    { title: '' }
  ];

  return (
    <div className="table-ready">
      <RoleBasedRender requiredRoles={ [''] } >
        <Table 
          columns={ columns } 
          data={ objects } 
          onDelete= { onDelete }
          onRowClick={ onRowClick }
        />
      </RoleBasedRender>
      <RoleBasedRender requiredRoles={ [''] } >
        <Table 
          columns={ columns2 } 
          data={ objects } 
          onDelete= { onDelete }
          onRowClick={ onRowClick }
        />
      </RoleBasedRender>
    </div>
  );
}

export default ReadyTable;
