import React from 'react';
import Table from '../../Table/Table';
import { formatDate } from '../../../utils';

function UsersTable({ users, onRowClick, onDelete }) {
  const columns = [
    { title: '' },
    { title: '', format: address => `${ address.substring(0,7) }...${ address.slice(address.length - 5) }`},
    { title: '' },
    { title: '' },
    { title: ' ', format: date => formatDate(date) },
  ];

  const actions = [
    <button className='secondary-button hidden' onClick={ onDelete }></button>
  ];

  return (
    <Table 
      columns={ columns } 
      data={ users } 
      onRowClick={ onRowClick } 
      actions={ actions } 
    />
  );
}

export default UsersTable;
