import React from 'react';
import Table from '../../Table/Table';

function PermissionsTable({ permissions, onRowClick, onDelete }) {
  const columns = [
    { 
      title: '', 
      subColumns: [
        { title:'', accessor: value => value.name }, 
        { title:'', accessor: value => value.mail }, 
        { title:'', accessor: value => value.address, format: address => `${ address.substring(0,7) }...${ address.slice(address.length - 5) }` }
      ] 
    },
    { title: ' ', format: value => value.toLocaleString() }
  ];
  const actions = [
    <button className='secondary-button hidden' onClick={ onDelete }></button>
  ];

  return (
    <Table
      columns={ columns }
      data={ permissions }
      actions={ actions }
      onRowClick={ onRowClick }
    />
  );
}

export default PermissionsTable;