import React from 'react';
import Table from '../../Table/Table';

function ObjectsTable({ objects, onRowClick, onDelete }) {
  const columns = [
    { title: '' },
    { title: ' '},
    { title: ' ' },
    { title: ' ' },
    { title: ' '},
  ];

  const actions = [
    <button className='secondary-button hidden' onClick={ onDelete }></button>
  ];

  return (
    <Table 
      columns={ columns } 
      data={ objects } 
      onRowClick={ onRowClick } 
      actions={ actions } 
    />
  );
}

export default ObjectsTable;
